<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { onMounted, toRef, ref } from 'vue'
import { reactive, } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import dayjs from 'dayjs'
import axios from 'axios'

export default {
  name: "TheComment",
  props: ['answerid', 'qnickname', 'quid', 'qemail'],
  components: { Modal, },


  setup(props) {

    onMounted(() => { loginHandler(), countComment() })

    const router = useRouter()
    const aid = toRef(props, 'answerid')
    const quid = toRef(props, 'quid')
    const qemail = toRef(props, 'qemail')

    const state = reactive({
      aid: aid,
      qnickname: '', //댓글 추가시 로그인된 nickname 보여짐
      quid: quid,     //댓글 추가시 로그인된 uid로 댓글 추가
      qemail: qemail,
      cid: '',
      nickname: '',
      content: '',
      lists: '',
      modDate: '',
      comCount:'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal: false,
    });
    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YYYY-MM-D HH:mm');
    };

    const modal = reactive({
      commentid: '',
      nickname: '',
      content: ''
    })

    const sendId = (commentid, nickname, content) => {
      modal.commentid = commentid
      modal.nickname = nickname
      modal.content = content
    };


    const deleteModal = () => {
      ElMessageBox.confirm(
        'Do you really want to delete your comment?',
        'Warning',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
          deleteComment()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: 'Delete canceled',
          })
        })
    }


    const loginHandler = async () => {
      const url = "http://43.201.60.125/api/commentbyanswer"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { answer: state.aid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data
          // console.log(res.data);
        } else {
          alert('bad.')
        }
      })
    }

    const countComment = async () => {
    const url= "http://43.201.60.125/api/countComment"

    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token,
      'name': 'test list'
    }
      const body = { answerid: state.aid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status==200){
          state.comCount = res.data.comCount
          // console.log(state.comCount);;
        } else{
          alert('bad.')
        }
      })
    }

    const deleteComment = async () => {
      const url = 'http://43.201.60.125/api/deleteComment'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { commentid: state.cid, }
      console.log("delete comment... launch  " + state.cid)
      await axios.post(url, body, { headers }).then(function (res) {
        console.log("delete comment after axios")
        if (res.status === 200) {
          console.log(`comment${state.cid} is deleted`);
          setTimeout(loginHandler(),1000);
        }
      })
    }

    // const modifyComment = async () => {
    //   if (modal.content === '') {
    //     alert("Please input content"); content.value.focus(); return false;
    //   }
    //   console.log("modify Answer launch")
    //   const url = "http://43.201.60.125/api/modifyComment"
    //   const headers = {
    //     "Content-Type": "application/json",
    //     Authorization: state.token,
    //     "token": state.token,
    //   }
    //   const body = {
    //     commentid: modal.commentid,
    //     nickname: modal.nickname,
    //     content: modal.content,
    //     modDate: state.modDate
    //   }
    //   const response = await axios.post(url, body, { headers })
    //   if (response.status === 200) {
    //     alert('modify successfully');
    //     setTimeout(loginHandler(),1000);
    //   } else {
    //     alert('fail')
    //   }
    // }

    const regist = reactive({
      registContent: '',
    });

    const commentRegister = async () => {
      if (regist.registContent === '') {
        alert('Please Input your content'); email.value.focus(); return false;
      }
      const url = 'http://43.201.60.125/api/commentRegister'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = {
        answerid: state.aid,
        uid: state.quid,
        content: regist.registContent,
      }
      const response = await axios.post(url, body, { headers })
      console.log(response.data)
      if (response.status === 200) {
        alert('register successfully');

        state.showModal = false;
        setTimeout(loginHandler(),1000);
      } else {
        alert('fail')
      }
    }



    return { formatDate, countComment, deleteModal, state, loginHandler, deleteComment, modal, sendId, regist, commentRegister }

  }


};

</script>

